import React from 'react';
import { Router,Route, Switch } from 'react-router-dom';
import ProfilePage from './pages/ProfilePage';
import FxIndexPage from './pages/FxIndexPage';
import TableDetailView from './pages/tablebox/TableDetailView';
// import DashboardPage from './pages/DashboardPage';
// import TablesPage from './pages/TablesPage';
// import MapsPage from './pages/MapsPage';
// import NotFoundPage from './pages/NotFoundPage';
import history from '../history';


class Routes extends React.Component {
  render() {
      console.log("this.props.history====" , this.props.history);
    return (
      <Switch>

        {/*<Route path='/' exact component={DashboardPage} />*/}
        {/*<Route path='/dashboard' component={DashboardPage} />*/}
        <Route path='/' exact
               // component={ProfilePage}
               component={() => <ProfilePage history={this.props.history} />}
               history={this.props.history}/>
        <Route path='/tableview' exact strict
               // component={TableDetailView}
               component={() => <TableDetailView history={this.props.history} />}
               history={this.props.history}/>
        <Route path='/fxindex' exact strict
               component={() => <FxIndexPage history={this.props.history} />} />
        {/*<Route path='/tables' component={TablesPage} />*/}
        {/*<Route path='/maps' component={MapsPage} />*/}
        {/*<Route path='/404' component={NotFoundPage} />*/}

      </Switch>
    );
  }
}

export default Routes;
