import React from 'react';
import './style.css';
import {
    List ,ListItem,
    Button,Menu,MenuItem,
    Popover,Icon,IconButton,
    Grid
}from '@material-ui/core';
import TopNavigation from '../../../components/topNavigation';
import SideNavigation from '../../../components/sideNavigation';
import Footer from '../../../components/Footer';
import Dropdown from "../../Dropdown";
import Images from './Images';
import Draggable from 'react-draggable';


class TableDetailView extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            params : (props.history && props.history.location && props.history.location.state ) ? props.history.location.state : null,
            imageList: [],
            selectedboard: null,
            selectedimage : null,
            selectedimagename : null,
            changedimage: false,
            slotstatus : 'all',
            selectedslot : [],
        };
    };

    componentDidMount(){
        this.makeimagelist();

    }
    componentDidUpdate(prevProps, prevState, snapshot){

        if( this.props.history && this.props.history.location && this.props.history.location.state &&  JSON.stringify(this.state.params) !== JSON.stringify(this.props.history.location.state)){
            this.setState({params:this.props.history.location.state});
            this.setState({changedimage: false});
            this.initstatus();

        }
        if(JSON.stringify(this.state.params) !== JSON.stringify(prevState.params)){
            this.makeimagelist();
        }
        if(this.props.history && this.props.history.location !== prevProps.history.location){
            this.setState({changedimage: true});
            this.makeimagelist();

        }
    }
    initstatus=()=>{
        this.clearslot();
        this.setState({slotstatus:'all'});
        this.setState({selectedboard:null});
        this.setState({selectedimage:null});
        this.setState({selectedimagename:null});

    }
    makeimagelist=()=>{
        let imagelist =[];
        if(this.state.params && this.state.params.menu){
            if(this.state.params.menuitem && Images[this.state.params.menu] && Images[this.state.params.menu]['AC'+this.state.params.menuitem]){

                let midname = Images[this.state.params.menu]['AC'+this.state.params.menuitem];
                if(midname && midname.length > 0){
                    midname.map((_image)=>{
                        let midobj={};
                        let midurl = '/assets/images/'+this.state.params.menu+'/'+_image.name+'/'+_image.name;
                        midobj.name =_image.name;
                        midobj.all = midurl+'_allwhite.png';
                        midobj.white = midurl+'_white.png';
                        midobj.black = midurl+'_black.png';
                        midobj.slot = _image.slot;
                        midobj.blank = _image.blank;
                        imagelist.push(midobj);
                    });

                }
            }
            this.setState({imageList:imagelist });

        }
    }
    leftimageclicked=(image)=>{

        this.setState({selectedboard: image});
        this.setState({selectedimage: image.all});
        this.setState({selectedimagename: image.name});
        this.setState({changedimage: true});

    }
    colorchange=(color)=>{
        this.setState({slotstatus: color});
        if(color==='black'){
            this.setState({selectedimage: this.state.selectedboard.black});
        }
        if(color==='white'){
            this.setState({selectedimage: this.state.selectedboard.white});
        }
        this.clearslot();
    }
    selectslot=(slot)=>{
        if(slot && slot.name && slot.name !== null ){
            let midslot = this.state.selectedslot;
            if(this.state.selectedboard && this.state.selectedboard.blank ){
                if(midslot.length < this.state.selectedboard.blank){
                    midslot.push(slot);
                    this.calcslotposition(this.state.selectedboard.slot,this.state.selectedboard.blank,midslot.length-1);
                }
            }
            this.setState({selectedslot: midslot});
        }
    }
    calcslotposition=(slot,blank,num)=>{
        let mainimage = document.getElementsByClassName('mainimage')[0];
        let box;
        if(mainimage && mainimage !== null){
            box = mainimage.getBoundingClientRect();


        }
        if(this.state.params.menu==='Clamp'){
            let y = -277 +(-160)*num;
            let x = (slot *1.5 - blank +1)*39 -9+ num * 76-(slot-3) * 2;
            this.setState({['slotposition'+num+'x']:x});
            this.setState({['slotposition'+num+'y']:y});
        }
        else if(this.state.params.menu ==='InTable'){
            let y = -277 +(-160)*num;
            let x = (slot *1.5 - blank +1)*39 -9+ num * 76+slot * 0.9+(blank-1)*2;
            this.setState({['slotposition'+num+'x']:x});
            this.setState({['slotposition'+num+'y']:y});
        }else if(this.state.params.menu ==='UnderTable'){
            let y = -277 +(-160)*num;
            let x = (slot *1.5 - blank +1)*39 -9+ num * 76-(slot-3) * 2;
            this.setState({['slotposition'+num+'x']:x*-1});
            this.setState({['slotposition'+num+'y']:y});
        }else if(this.state.params.menu ==='DualInTable'){
            let y = -277 +(-160)*num;
            let x = (slot *1.5 - blank +1)*39 -9+ num * 76+slot * 0.9+(blank-1)*2;
            this.setState({['slotposition'+num+'x']:x});
            this.setState({['slotposition'+num+'y']:y});
        }
    }
    slotremove=(n)=>{

        const {selectedslot} = this.state;
        let midselectedslot = [];
        if(selectedslot && selectedslot.length >0){
            selectedslot.map((slot,index)=>{
               if(n !== index){
                   midselectedslot.push(slot)
               };
            });
            this.setState({selectedslot: midselectedslot});
        }
    }
    clearslot=()=>{
        this.setState({selectedslot: []});
    }
    render() {


        let midmenu = this.state.params.menu;
        if(midmenu ==='Clamp'){
            midmenu = 'Clamp - On';
        }else if(midmenu ==='InTable'){
            midmenu = 'In - Table';
        }else if(midmenu ==='UnderTable'){
            midmenu = 'Under - Table';
        }else if(midmenu ==='DualInTable'){
            midmenu = 'Dual - In - Table';
        }
        return (
            <div className="flexible-content">
                <TopNavigation/>
                <SideNavigation history={this.props.history}/>
                <main id="content" style={{height: '-webkit-fill-available'}}>

                        <Grid container>
                            <Grid item sm={1} >
                                <div className="leftside" style={{height: '-webkit-fill-available'}}>
                                    <div className="title">
                                        {(this.state.params !==null && this.state.params.menu && this.state.params.menuitem ) ? (

                                                <div>
                                                    <div>
                                                        <span style={{fontSize: 20,fontWeight: 600}}>{midmenu} </span>
                                                        <br/>
                                                        <span style={{fontSize: 20,fontWeight: 600}}> {this.state.params.menuitem} - AC</span>
                                                    </div>
                                                    <div>
                                                        { !this.state.changedimage && this.state.imageList && this.state.imageList.length > 0 && this.state.imageList.map((_image,index)=>
                                                            (
                                                                <div key={index}>
                                                                    <Button onClick ={()=>this.leftimageclicked(_image)}>
                                                                        <img src={process.env.PUBLIC_URL+_image.all} alt="" style={{width: '100%',height:'auto'}}/>
                                                                    </Button>
                                                                </div>
                                                            ))

                                                        }
                                                        {this.state.changedimage && (
                                                            <div className="slotview">
                                                                <div style={{fontSize:25,fontWeight: 700}}>Slot</div>
                                                                {this.state.slotstatus && Images['Slot'][this.state.slotstatus] && Images['Slot'][this.state.slotstatus].length > 0 && Images['Slot'][this.state.slotstatus].map((slot,index)=>(
                                                                    <div key={index}>
                                                                        <Button onClick ={()=>this.selectslot(slot)}>
                                                                            <img src={process.env.PUBLIC_URL+'/assets/images/Slot/'+slot.name} alt="" style={{width: '100%',height:'auto'}}/>
                                                                        </Button>
                                                                    </div>
                                                                ))}
                                                                <div style={{fontSize:25,fontWeight: 700}}>Color</div>
                                                                {this.state.slotstatus && Images['Color'] && (
                                                                    <div >
                                                                        <div>
                                                                            <div style={{fontSize:18,fontWeight: 600}}>Black w/ Aluminum</div>
                                                                            <Button className="coloritems" onClick={()=>this.colorchange('black')}>
                                                                                <img src={process.env.PUBLIC_URL+'/assets/images/Color/'+Images['Color'].black.name} alt="" style={{width: '100%',height:'auto'}}/>
                                                                            </Button>
                                                                        </div>
                                                                        <div>
                                                                            <div style={{fontSize:18,fontWeight: 600}}>White w/ Aluminum</div>
                                                                            <Button className="coloritems" onClick={()=>this.colorchange('white')}>
                                                                                <img src={process.env.PUBLIC_URL+'/assets/images/Color/'+Images['Color'].white.name} alt="" style={{width: '100%',height:'auto'}}/>
                                                                            </Button>
                                                                        </div>

                                                                    </div>
                                                                )}
                                                            </div>
                                                        )}
                                                    </div>
                                                </div>

                                        ) :(

                                            <div/>

                                        )}

                                    </div>
                                </div>
                            </Grid>
                            <Grid item sm={11} >
                                <div className="rightside" style={{height: '-webkit-fill-available'}}>
                                    <div style={{height: 50,backgroundColor:'#e5e8e7'}}>
                                        <div style={{fontSize: 20 , fontWeight: 700,color:'#087504',paddingTop: 7,paddingLeft : 10}}>Select a Table Box Option</div>
                                    </div>
                                    <div className="mainimageview">
                                        <br/>
                                        <div>
                                            <img src={process.env.PUBLIC_URL+this.state.selectedimage} className="mainimage" alt=""/>
                                        </div>
                                        <div>{this.state.selectedimagename && this.state.selectedimagename !== null && (
                                            <div style={{fontSize: 25,fontWeight:700,}}>{this.state.selectedimagename}</div>
                                        )}
                                        </div>

                                        {this.state.selectedimagename && this.state.selectedimagename !== null && (
                                            <div>
                                                <br/>
                                                <Button variant="contained" color="primary" style={{marginRight: 20}}>
                                                    Submit
                                                </Button>
                                                <Button variant="contained" onClick ={this.clearslot}>
                                                    Clear
                                                </Button>
                                            </div>
                                        )}
                                        {this.state.selectedslot && this.state.selectedslot !== null && this.state.selectedslot.length > 0 && this.state.selectedslot.map((slot,index)=>(
                                            <div key={index}>
                                                <Draggable
                                                    handle=".handle"
                                                    defaultPosition={{x: 0, y: 0}}
                                                    position={{x: this.state[`slotposition${index}x`] ? this.state[`slotposition${index}x`] : 0 , y:this.state[`slotposition${index}y`] ? this.state[`slotposition${index}y`] : -277}}
                                                    scale={1}
                                                    // onStart={(e)=>this.handleStart(e,item.trx_group_name+'-0',item.trx_group_name+'0')}
                                                    // onDrag={(e)=>this.handleDrag(e,item.trx_group_name+'0')}
                                                    // onStop={(e)=>this.handleStop(e,item.trx_group_name+'0',selectedtabid,item.trx_group_name,item.trx_group_id,0)}

                                                >
                                                    <div
                                                        // onDoubleClick={(e)=>this.dbClicked(e,item.trx_group_name+'0')}
                                                    >

                                                        <div
                                                            className="handle"
                                                        >

                                                            <Button >
                                                                <img src={process.env.PUBLIC_URL+'/assets/images/Slot/'+slot.name} onClick={()=>this.slotremove(index)} style={{width: '100%',height:'auto'}}/>
                                                            </Button>
                                                        </div>

                                                    </div>
                                                </Draggable>

                                            </div>
                                        ))}

                                    </div>
                                </div>


                            </Grid>
                        </Grid>

                </main>
                <Footer/>
            </div>

        );
    }
}

export default TableDetailView;