import React from 'react'
import { MDBCard, MDBCol, MDBRow, MDBView, MDBMask, MDBCardImage, MDBCardBody, MDBCardTitle, MDBCardText, MDBCardFooter, MDBBtn, MDBIcon } from 'mdbreact';
import ProductData from '../../data/products'
import TopNavigation from '../../components/topNavigation';
import SideNavigation from '../../components/sideNavigation';
import Footer from '../../components/Footer';

class ProfilePage extends React.Component {
    constructor(props){
        super(props);
        this.state = {

        };


    };
    render() {
        return (
            <div className="flexible-content">
                <TopNavigation />
                <SideNavigation history={this.props.history}/>
                <main id="content" className="p-5">
                    <React.Fragment>
                        <MDBRow className="justify-content-center">
                            <MDBCol md="12" lg="12">
                                <section className="text-center pb-3">
                                    <MDBRow className="d-flex justify-content-center">
                                        {ProductData.map((postDetail, index) => {
                                            return <MDBCol lg="6" xl="5" className="mb-3"   key ={index}>
                                                <MDBCard className="d-flex mb-5">
                                                    <MDBView>
                                                        <img src={postDetail.image} alt="Project" className="img-fluid"/>
                                                        <MDBMask overlay="white-slight"/>
                                                    </MDBView>
                                                    <MDBCardBody>
                                                        <MDBCardTitle className="font-bold mb-3">
                                                            <strong>{postDetail.title}</strong>
                                                        </MDBCardTitle>
                                                        <MDBCardText>{postDetail.content}</MDBCardText>
                                                    </MDBCardBody>
                                                </MDBCard>
                                            </MDBCol>
                                        })}
                                        {/*</MDBRow>*/}
                                        {/*<MDBRow className="d-flex justify-content-center">*/}
                                    </MDBRow>
                                </section>
                            </MDBCol>
                        </MDBRow>
                    </React.Fragment>
                </main>
                <Footer />
            </div>
        );
    }

}






export default ProfilePage;