import React from 'react';
import './style.css';
import {
    List ,ListItem,
    Button,Menu,MenuItem,
    Popover,Icon,IconButton
        }from '@material-ui/core';


import  { Redirect } from 'react-router-dom';

class Dropdown extends React.Component {
    constructor(props){
        super(props);

        this.state = {
            displayMenu: false,
            menuitem : null,
            ClampOn : null,
            Clamitem : null,


            InTable : null,
            InTableitem:null,


            UnderTable : null,
            UnderTableitem : null,


            DualInTable : null,
            DualInTableitem : null,
        };

        this.showDropdownMenu = this.showDropdownMenu.bind(this);
        this.hideDropdownMenu = this.hideDropdownMenu.bind(this);

    };

    showDropdownMenu(event) {
        event.preventDefault();
        this.setState({ displayMenu: true }, () => {
            document.addEventListener('click', this.hideDropdownMenu);
        });
    }

    hideDropdownMenu() {
        // this.setState({ displayMenu: false }, () => {
        //     document.removeEventListener('click', this.hideDropdownMenu);
        // });

    }
    menuclicked=(name,num)=>{
        if(name){
            let check = true;
            if(this.state[name]===null ||this.state[name]){
                check = false;
            }
            this.setState({menuitem: num});
            this.setState({[name]: check});
        }
    }
    clampon=event=>{
        console.log("clampon===");
        this.setState({ClampOn: event.currentTarget});
    }
    clamponclose=()=>{
        this.setState({ClampOn: null});
    }
    clamponmenu=(num)=>{
        console.log("num==" ,num);
        if(num){
            this.setState({Clamitem :num});
        }

        this.props.history.push({
            pathname: '/tableview',
            state: {
                menu: 'Clamp',
                menuitem : num,
                // history: this.props.history,
            }
        })
        // this.props.history.push('/tableview');
    }

    InTable=event=>{
        this.setState({InTable: event.currentTarget});
    }
    InTableclose=()=>{
        this.setState({InTable: null});
    }
    InTablemenu=(num)=>{
        this.setState({InTableitem: num});
        this.props.history.push({
            pathname: '/tableview',
            state: {
                menu: 'InTable',
                menuitem : num,
                // history: this.props.history,
            }
        });
    }

    UnTable=event=>{
        this.setState({UnderTable: event.currentTarget});
    }
    UnTableclose=()=>{
        this.setState({UnderTable: null});
    }
    UnTablemenu=(num)=>{
        this.setState({UnderTableitem: num});
        this.props.history.push({
            pathname: '/tableview',
            state: {
                menu: 'UnderTable',
                menuitem : num,
                // history: this.props.history,
            }
        })
    }
    DualInTable=event=>{
            this.setState({DualInTable: event.currentTarget});
    }
    DualInTableclose=()=>{
            this.setState({DualInTable: null});
    }
    DualInTablemenu=(num)=>{
            this.setState({DualInTableitem: num});
        this.props.history.push({
            pathname: '/tableview',
            state: {
                menu: 'DualInTable',
                menuitem : num,
                // history: this.props.history,
            }
        })
    }

    render() {
        const {ClampOn,InTable,UnderTable,DualInTable} = this.state;
        return (
            <div className="container">
                <div className="dropdown sidebar">
                    <button className="btn btn-default dropdown-toggle" onClick={this.showDropdownMenu}>
                        Table Options
                    </button>


                { this.state.displayMenu ? (
                    <div style={{display:'flex',flexDirection: 'column'}}>
                        <Button aria-controls="simple-menu" aria-haspopup="true" className ="menubtn"  onClick={this.clampon}>
                            Clamp-On
                        </Button>
                        <Button aria-controls="simple-menu" aria-haspopup="true" className ="menubtn"  onClick={this.InTable}>
                            In-Table
                        </Button>
                        <Button aria-controls="simple-menu" aria-haspopup="true" className ="menubtn"  onClick={this.UnTable}>
                            Under-Table
                        </Button>
                        <Button aria-controls="simple-menu" aria-haspopup="true" className ="menubtn"  onClick={this.DualInTable}>
                            Dual-In-Table
                        </Button>

                        <Popover
                            open={Boolean(ClampOn)}
                            anchorEl={ClampOn}
                            onClose={this.clamponclose}
                            anchorOrigin={{
                                vertical  : 'top',
                                horizontal: 'right'
                            }}
                            transformOrigin={{
                                vertical  : 'top',
                                horizontal: 'left'
                            }}
                            classes={{
                                paper: "py-8",
                            }}

                        >
                            <React.Fragment>
                                <MenuItem value={"1"} className={""}  style={{backgroundColor: 'gray'}} onClick={()=>{this.clamponmenu(2)}}>2-AC</MenuItem>
                                <MenuItem value={"2"} className={""}  style={{backgroundColor: 'gray'}} onClick={()=>{this.clamponmenu(3)}}>3-ACs</MenuItem>
                                <MenuItem value={"3"} className={""}  style={{backgroundColor: 'gray'}} onClick={()=>{this.clamponmenu(4)}}>4-ACs</MenuItem>
                                <MenuItem value={"4"} className={""}  style={{backgroundColor: 'gray'}} onClick={()=>{this.clamponmenu(5)}}>5-ACs</MenuItem>
                            </React.Fragment>

                        </Popover>
                        <Popover
                            open={Boolean(InTable)}
                            anchorEl={InTable}
                            onClose={this.InTableclose}
                            anchorOrigin={{
                                vertical  : 'top',
                                horizontal: 'right'
                            }}
                            transformOrigin={{
                                vertical  : 'top',
                                horizontal: 'left'
                            }}
                            classes={{
                                paper: "py-8",
                            }}

                        >
                            <React.Fragment>
                                <MenuItem value={"1"} className={""} style={{backgroundColor: 'gray'}} onClick={()=>{this.InTablemenu(2)}}>2-AC</MenuItem>
                                <MenuItem value={"2"} className={""} style={{backgroundColor: 'gray'}} onClick={()=>{this.InTablemenu(3)}}>3-ACs</MenuItem>
                                <MenuItem value={"3"} className={""} style={{backgroundColor: 'gray'}} onClick={()=>{this.InTablemenu(4)}}>4-ACs</MenuItem>
                            </React.Fragment>

                        </Popover>


                        <Popover
                            open={Boolean(UnderTable)}
                            anchorEl={UnderTable}
                            onClose={this.UnTableclose}
                            anchorOrigin={{
                                vertical  : 'top',
                                horizontal: 'right'
                            }}
                            transformOrigin={{
                                vertical  : 'top',
                                horizontal: 'left'
                            }}
                            classes={{
                                paper: "py-8",
                            }}

                        >
                            <React.Fragment>
                                <MenuItem value={"1"} className={""} style={{backgroundColor: 'gray'}} onClick={()=>{this.UnTablemenu(2)}}>2-AC</MenuItem>
                                <MenuItem value={"2"} className={""} style={{backgroundColor: 'gray'}} onClick={()=>{this.UnTablemenu(3)}}>3-ACs</MenuItem>
                                <MenuItem value={"3"} className={""} style={{backgroundColor: 'gray'}} onClick={()=>{this.UnTablemenu(4)}}>4-ACs</MenuItem>
                                <MenuItem value={"3"} className={""} style={{backgroundColor: 'gray'}} onClick={()=>{this.UnTablemenu(5)}}>5-ACs</MenuItem>
                            </React.Fragment>

                        </Popover>

                        <Popover
                            open={Boolean(DualInTable)}
                            anchorEl={DualInTable}
                            onClose={this.DualInTableclose}
                            anchorOrigin={{
                                vertical  : 'top',
                                horizontal: 'right'
                            }}
                            transformOrigin={{
                                vertical  : 'top',
                                horizontal: 'left'
                            }}
                            classes={{
                                paper: "py-8",
                            }}

                        >
                            <React.Fragment>
                                <MenuItem value={"1"} className={""} style={{backgroundColor: 'gray'}} onClick={()=>{this.DualInTablemenu(2)}}>2-AC</MenuItem>
                                <MenuItem value={"2"} className={""} style={{backgroundColor: 'gray'}} onClick={()=>{this.DualInTablemenu(3)}}>3-ACs</MenuItem>
                                <MenuItem value={"3"} className={""} style={{backgroundColor: 'gray'}} onClick={()=>{this.DualInTablemenu(4)}}>4-ACs</MenuItem>
                            </React.Fragment>

                        </Popover>


                    </div>
                    ):
                    (
                        null
                    )
                }

                </div>
            </div>

        );
    }
}

export default Dropdown;